*{
    box-sizing: border-box;
    margin: 0%;
}
.main{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 600px;
    background-color: black;
    width: 100%;
}
.banner{
    background-image: url("../component/pic/me-2.jpg");
    width: 300px;
    height: 350px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
}
.info_home{
    color: aliceblue;
    padding: 10px;
}
.top_sociallink{
    color: aliceblue;
    display: flex;
    flex-direction: row;
    /* width: 100px; */
    align-items: center;
    /* justify-content: center; */
}
.sociallink{
    color: aliceblue;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-left: 10px;
    margin-left: 10px;
    font-size: 30px;
    /* width: 30px; */
    height: 30px;
    transition:  500ms;
}
.Typewriter{
    width: 200px;
}
#linkdin:hover{
    color: #0077b5;
    background: white;
    transition: 500ms;
    /* font-size: 35px; */
}
#instagram{
    transition: 500ms;
}
#instagram:hover , #instagrame:focus{
    /* background: linear-gradient(rgb(5 10 230) , rgb(88 81 216),rgb(131 58 180),rgb(193 53 132),rgb(225 48 108),rgb(253 36 76),rgb(247 119 55),rgb(252 175 69),rgb(255 220 128)); */
    background: linear-gradient(rgb(5 10 230),rgb(255, 106, 106),red,yellow);
    color: white;
    transition:  500ms;
}

#facebook:hover{
    background: white;
    color:  #316FF6;
    transition: 500ms;
}
#github:hover{
    background:white;
    color: black;
    transition: 500ms;
}
.hire_button{
    background-color: rgba(223, 8, 4, 0.941);
    cursor: pointer;
    height: 30px;
    width: 100px;
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}
@media only screen and (max-width: 450px)
{
    .main{
        display: flex;
        flex-direction: column;
    }
    .banner{
        width: 250px;
    }

}