*{
    box-sizing: border-box;
    margin: 0%;
}
.resume_body{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    flex-direction: row;
    height: 600px;
    width: 100%;
    /* padding-top: 30px; */
    background-color: black;
    color: rgb(255, 255, 255);
    /* overflow-y: auto; */
}
.slider_button{
    display: flex;
    flex-direction: column;
    cursor: pointer;
}
/* .slider_button svg{
    padding: 10px;
    height: 15px;
    width: 15px;
    color: aliceblue;
} */
.resume-section{
/* display: flex;
flex-direction: column; */
/* position:relative; */
padding: 10px;
justify-content: center;
width: 400px;
align-items: center;
overflow-y: auto;
overflow-x: hidden;
overflow: scroll;
height: 400px;
box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.resume-section::-webkit-scrollbar{
    display: none;
}
#slider_section{
    height: 400px;
    width: 400px;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    /* align-items: center; */
    /* margin: 20px; */
    /* padding: 20px; */
}
#slider_section{
    text-align: left;
}
.resume_Unit_option{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.resume_option{
    margin: 20px;
    list-style-type: none;
}
/* .underline_resume{
    z-index: 2px;
} */
.active > .underline_resume{
    height: 5px;
    width: 100%;
    background-color: brown;
    border-radius: 2px;
}
.resume-section{
    border-radius: 21px;
background: #000000;
box-shadow:  5px 5px 6px #767676,
             -5px -5px 3px #0b0b0b;
}
@media only screen and (max-width: 450px)
{
    .resume_Unit_option{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 80px;
        padding: 0;
    }
    .resume_option{
        list-style-type: none;
        font-size: 11px;
        margin: 0;
        /* margin-right: 20px; */
        margin-top: 20px;
        padding: 10px;
    }
    #slider_section{
        font-size:x-small;
    }
    #slider_section > p , h1 , h2 , h3,h4{
        width: 50%;
    }
    
}