*{
    box-sizing: border-box;
    margin: 0%;
    /* display: flex; */
}
.header{
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
    position: fixed;
    z-index: 2;
    background-color: rgba(255, 255, 255, 0);
    height: 40px;
    
}
.nav_options{
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.humburger_mob{
    display: none;
}
.Options{
    display: flex;
    flex-direction: row;
    width: 100%;
    color: aliceblue;
}
.menu {
    list-style: none;
    display: inline-block;
    width: 100%;
    margin-right:  10px;
    margin-left: 10px;
    cursor: pointer;
  }
/* .underline{
    height: 2px;
    width: 5px;
} */
.active >.underline{
    height: 5px;
    width: 100%;
    background-color: brown;
    border-radius: 2px;
}
/* li:hover {
    /* background-color: antiquewhite; */
    /* box-shadow: 10px 2px 10px rgba(0, 0, 0, 0.521); 
    text-shadow: 5px 7px 10px black;
} */
@media only screen and (max-width: 450px){
    .nav_options{
        display: none;
    }
    .nav_options_active{
        z-index: 3;
        height: 200px;
        width: 100%;
        background-color: rgba(0, 0, 0, 0.599);
        display: flex;
        flex-direction: column;
        
        justify-content: center;
        /* align-items: center; */
        
    }
    .Options{
        display: flex;
        flex-direction: column;
        width: 100px;
        margin-top: 5px;
        padding: 0px;
        font-weight: 400;
    }
    .humburger_mob{
        display: flex;
        color: aliceblue;
        justify-content: center;
        align-items: center;
        flex-direction: row-reverse;
        z-index: 10;
        padding: 10px;

    }
    .underline{
        display: none;
    }
    .active{
        color: brown;
    }
}

