*{
    box-sizing: border-box;
    margin: 0%;
}
.contact_main{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 450px;
    width: 100%;
    background-color: black;
    color: white;
    /* border:solid 2px white; */
}
.contact_main{
    box-shadow: inset;
    height: 40rem;
}
.contact_container{
    display: flex;
    align-items: center;
    justify-content: center;
    /* width: 50rem; */
    height: 20rem;
}
/* .from_main > h1{

} */
.from_main  hr{
    width: 100px;
    border: none;
    border-radius: 2px;
    border: solid rgb(255, 74, 3);
    border-radius: 5px;
    margin-top: 5px;
}
.from_main{
   
    display: flex; 
    flex-direction: column;
    height: 20rem;
    gap: 20px;
    
}
.contact_image{
    width: 10rem;
    z-index: 2;
}
.Text{
    border: none;
    outline: none;
    height: 30px;
    border-radius: 5px;
}
.Textarea{
    border: none;
    outline: none;
    /* height: 30px; */
    border-radius: 5px;
}
.contact_submit{
    border: none;
    outline: none;
    border-radius: 3px;
    width: 100px;
    height: 2rem;
    background-color: brown;
    color: aliceblue;
    transition: ease-in-out 400ms;
}
.contact_submit:hover{
    border-top-color: rgb(183, 57, 3);
    background-color: rgb(255, 255, 255);
    color: brown;
    width: 105px;
    /* height: 2.5rem; */
    transition: ease-in-out 400ms;
}

.down_sociallink{
    display: flex;
    flex-direction: column;
}
.contact_sociallink{
    color: aliceblue;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 30px;
    /* width: 30px; */
    height: 30px;
}
.upbutton{
    background-color: rgba(240, 248, 255, 0.567);
    color: rgb(0, 0, 0);
    border-radius: 50% 50%;
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 19px;
    margin-left: 70px;
}


@media only screen and (max-width: 480px){
    .contact_main{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 45rem;
        width: 100%;
        background-color: black;
        color: white;
        /* border:solid 2px white; */
    }
    .contact_container{
        display: flex;
        flex-direction: column;
        height:auto;
    }
    .down_sociallink{
        display: flex;
        flex-direction: row;
        gap: 10px;
    }
    .contactimage{
        width: 10rem;
        height: 10rem;
    }
    /* .from_main{
   
        display: flex; 
        flex-direction: column;
        height: 15rem;
        gap: 20px;
        
    } */
   .Textarea{
    height: 100px;
   }
   .upbutton{
    margin: 0;
    /* margin-left: 70px; */
    }
}