*{
    box-sizing: border-box;
    margin: 0%;
}
.about{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 600px;
    width: 100%;
    background-color: black;
}

.about_banner{
    display: flex;
    flex-direction: row;
    padding: 10px;
    margin-top: 10px;
    /* border:solid 1px rgb(255, 255, 255); */
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    color: aliceblue;
}
.about_banner{
    display: flex;
    flex-direction: row;
    padding: 10px;
    margin-top: 10px;
    /* border:solid 1px rgb(255, 255, 255); */
    height: 80%;
    width: 100%;
    justify-content: center;
    align-items: center;
    color: aliceblue;
}
.pic_section , .about_section{
    display: flex;
    flex-direction: row;
    padding: 40px;
    margin-top: 10px;
    /* border:solid 1px rgb(255, 255, 255); */
    height: 100%;
    /* width: 50%; */
    justify-content: center;
    align-items: center;
    color: aliceblue;
}
.about_picture{
    height: 250px;
    width: 200px;
    background-image: url("../component/pic/me.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    border-radius: 10px 10px 10px 10px;
    box-shadow: 1px 3px 4px white;
}
#about_text {
    font-size: 10px; /* 14px/16=0.875em */
    height: 150px;
    width: 200px;
    font-family: Helvetica, sans-serif;
    padding: 5px;
    text-align: justify;
    
}

.about_section{
    flex-direction: column;
}
.job_require{
    display: flex;
    flex-direction: column;
    height: 150px;
    width: 150px;
    align-items: baseline;
    margin-top: 40px;
    
}
#about_ul{
    padding: 0;
    margin: 0;
}
#about_requirs{
  color: red;
  font-size: 10px;
}
#about_li{
    color: aliceblue;
}
@media only screen and (max-width: 450px)
{
    .about{
        height: 700px;
    }
    .about_picture{
        height: 200px;
        width: 170px;
    }
    .about_banner{
        display: flex;
        flex-direction: column;
    }
    .about_section{
        width: 100%;
    }
    .job_require{
        height: 80%;
        margin-top: 45px;
    }
    #about_text{
        width: 250px;
    }
}
